.mian-list {
  display: flex;
}
.mian-list .list-left {
  width: 240px;
  height: calc(100vh - 140px);
  border-right: 1px solid #f0f0f0;
}
.mian-list .list-right {
  flex: 1 1;
  padding-left: 20px;
}
:where(.css-dev-only-do-not-override-2i2tap).ant-list .ant-list-item {
  display: block;
}
/*css主要部分的样式*/
/*定义滚动条宽高及背景，宽高分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar {
  width: 5px;
  /*对垂直流动条有效*/
  /*对水平流动条有效*/
  background: #000;
}
/*定义滚动条的轨道颜色、内阴影及圆角*/
::-webkit-scrollbar-track {
  border-radius: 3px;
}
::-webkit-scrollbar-button {
  background-color: 000;
}
::-webkit-resizer {
  background-color: #000000;
}
/*定义滑块颜色、内阴影及圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 7px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #000;
}
/*定义两端按钮的样式*/
::-webkit-scrollbar-button {
  background-color: #000;
}
/*定义右下角汇合处的样式*/
::-webkit-scrollbar-corner {
  background: khaki;
}
.list-group-item {
  color: #fff;
}
.serch-box {
  width: 100%;
  padding: 20px;
  border-bottom: 1px solid rgba(5, 5, 5, 0.06);
  box-sizing: border-box;
}
.serch-box .input-serch {
  margin-left: 200px;
}
.serch-box .button-serch {
  margin-left: 26%;
}
.ant-card {
  background: #000;
}
:where(.css-dev-only-do-not-override-2i2tap).ant-list .ant-list-item .ant-list-item-meta .ant-list-item-meta-title {
  color: #fff;
}
:where(.css-dev-only-do-not-override-2i2tap).ant-list .ant-list-item .ant-list-item-meta .ant-list-item-meta-title > a {
  color: #fff;
}
.ant-input-group-addon {
  color: #fff;
}
:where(.css-dev-only-do-not-override-2i2tap).ant-input-group .ant-input-group-addon {
  color: #fff;
}
:where(.css-dev-only-do-not-override-2i2tap).ant-list .ant-list-item {
  color: #fff;
}
:where(.css-dev-only-do-not-override-2i2tap).ant-list-split .ant-list-item {
  -webkit-border-after: 1px solid rgba(255, 255, 255, 0.89);
          border-block-end: 1px solid rgba(255, 255, 255, 0.89);
}
:where(.css-dev-only-do-not-override-2i2tap).ant-list-split .ant-list-item:last-child {
  -webkit-border-after: 1px solid rgba(255, 255, 255, 0.89);
          border-block-end: 1px solid rgba(255, 255, 255, 0.89);
}
