.mian-list {
  display: flex;
}
.mian-list .list-left {
  width: 240px;
  height: calc(100vh - 140px);
  border-right: 1px solid #f0f0f0;
}
.mian-list .list-right {
  flex: 1 1;
  padding-left: 20px;
}
.my-page-list .ant-list-item {
  display: flex !important;
}
/*css主要部分的样式*/
/*定义滚动条宽高及背景，宽高分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar {
  /*对垂直流动条有效*/
  height: 10px;
  /*对水平流动条有效*/
}
/*定义滚动条的轨道颜色、内阴影及圆角*/
::-webkit-scrollbar-track {
  border-radius: 3px;
}
/*定义滑块颜色、内阴影及圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 7px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #E8E8E8;
}
/*定义两端按钮的样式*/
::-webkit-scrollbar-button {
  background-color: #000;
}
/*定义右下角汇合处的样式*/
::-webkit-scrollbar-corner {
  background: khaki;
}
:where(.css-dev-only-do-not-override-2i2tap).ant-tabs .ant-tabs-tab-btn {
  color: #fff;
}
