.App {
  text-align: center;
  height: 100vh;
  overflow: hidden;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
:global(.ant-menu) {
  min-height: 100vh;
}
.menu-self {
  height: 100vh;
}
.main-box {
  width: 100%;
  display: flex;
  background: #000;
  height: 100vh;
}
.main-box .left-menu {
  width: 200px;
}
.main-box .content-box {
  flex: 1 1;
  padding: 0 16px;
  height: calc(100vh - 40px);
}
.main-box .card-self {
  height: calc(100vh - 140px);
}
header,
footer {
  width: 100%;
  height: 56px;
  line-height: 56px;
}
.header-left {
  width: 260px;
  display: unset;
  text-align: center;
  position: relative;
  height: 100%;
  float: left;
}
.header-left .img-box {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 0;
  margin-left: -20px;
  cursor: pointer;
}
.header-left .img-box img {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.header-left .img-title {
  position: absolute;
  left: 50%;
  bottom: -20px;
  margin-left: -30px;
  cursor: pointer;
  color: #fff;
}
.header-right {
  flex: 1 1;
}
.header-right button:nth-child(2) {
  float: right;
  margin-top: 15px;
  margin-right: 80px;
}
header {
  display: flex;
  padding: 10px 0;
}
footer {
  text-align: center;
}
.braavos-content {
  width: 100%;
  height: 56px;
}
.Argent-button {
  font-weight: 700;
  font-size: 30px;
  vertical-align: top;
  margin-left: 20px;
}
:global(.ant-card-body) {
  padding: 0 10px;
}
:where(.css-dev-only-do-not-override-2i2tap).ant-card .ant-card-body {
  padding: 0 10px;
}
